@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@1,200&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Prompt", sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#aboutMe {
  display: flex;
  flex-direction: column;
}

#aboutMe > h1 {
  color: #410033;
  font-weight: 800;
}

#aboutMe > h1 > hr {
  border-top: 2px solid #74025b;
}

#aboutMe > .bg-img {
  position: absolute;
  object-fit: cover;
  -webkit-animation: fade ease 1s;
          animation: fade ease 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-filter: blur(3px) grayscale(0.8);
          filter: blur(3px) grayscale(0.8);
  -webkit-mask-image: linear-gradient(to bottom, #000000 10%, transparent 100%);
          mask-image: linear-gradient(to bottom, #000000 10%, transparent 100%);
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
    width: 70%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    height: 70%;
  }
  100% {
    opacity: 1;
    width: 100%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    height: 100%;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
    width: 70%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    height: 70%;
  }
  100% {
    opacity: 1;
    width: 100%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    height: 100%;
  }
}

.about-container {
  padding: 2rem;
  height: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.holder {
  width: 60%;
  display: flex;
  background-image: linear-gradient(to right, #74025b 5%, #272727 70%);
  border-radius: 6px;
  padding: 1rem;
  border: 3px solid #74025b;
  -webkit-animation: holderOpen ease 2s;
          animation: holderOpen ease 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes holderOpen {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes holderOpen {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.about-p > p {
  font-size: large;
  padding: 0 2rem;
  color: #ffff;
  line-height: 2;
  -webkit-animation: aboutFade ease 3.2s;
          animation: aboutFade ease 3.2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  transition: 0.5s ease-in-out;
}

.about-p > p > span {
  font-family: sans-serif;
  color: #c575b4;
}

@-webkit-keyframes aboutFade {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes aboutFade {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.about-img {
  flex: 100% 1;
  max-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 3px;
  -webkit-animation: openImage ease-in-out 2s;
          animation: openImage ease-in-out 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  transition: 0.5s ease-in-out;
}

@-webkit-keyframes openImage {
  0% {
    max-width: 0px;
  }
  100% {
    max-width: 600px;
  }
}

@keyframes openImage {
  0% {
    max-width: 0px;
  }
  100% {
    max-width: 600px;
  }
}

.about-img > img {
  width: 350px;
  border-radius: 3px;
}

@media screen and (max-width: 1024px) {
  .holder {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .about-container {
    order: 2;
    padding: 0.5rem;
  }

  .about-p > p {
    font-size: 87%;
  }
}

@media screen and (max-width: 540px) {
  .about-container {
    align-items: flex-start;
  }

  .holder {
    background-image: linear-gradient(to bottom, #74025b 5%, #272727 50%);
    flex-direction: column;
    padding: 0.3rem;
    height: 85vh;
  }

  .about-img {
    max-height: 250px;
    max-width: 200px;
    align-items: flex-start;
  }

  .about-img > img {
    width: 100%;
  }

  .about-p {
    overflow-y: scroll;
    max-height: 50vh;
  }

  .about-p > p {
    padding: 0.5rem;
  }
}

#contact {
  display: flex;
  flex-direction: column;
}

#contact > h1 {
  color: #410033;
  font-weight: 800;
}

#contact > h1 > hr {
  border-top: 2px solid #74025b;
}

.contact-container {
  padding: 3rem;
  height: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container > form {
  background-color: #410033;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 3px;
  flex: 50% 1;
  height: 100%;
  position: relative;
}

.contact-container > form > label {
  color: #fff;
  font-size: large;
}

.contact-container > form > input,
textarea {
  margin: 1rem 0;
  padding: 1rem 0.5rem;
  font-size: medium;
  outline: none;
  color: #814f81;
  font-weight: 700;
  letter-spacing: 1px;
}

.contact-container > form > textarea {
  height: 300px;
}

.contact-container > form > input[type="submit"] {
  background-color: none;
  border: 1px solid #fff;
  border-radius: 3px;
  color: #74025b;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

.contact-container > form > input[type="submit"]:hover {
  color: #a38605;
  letter-spacing: 3px;
  border: 1px solid #a38605;
  background-color: #272727;
}

.handles {
  flex: 50% 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #410033;
  height: 100%;
  margin-left: 1rem;
  border-radius: 3px;
}

.handles > a {
  color: #a38605;
  font-size: x-large;
  text-decoration: none;
  display: flex;
  align-items: center;
  margin: 1rem;
}

.handles > a > svg {
  margin-right: 0.5rem;
}

.handles > a:hover {
  color: #fff;
}

@media screen and (max-width: 768px) {
  .contact-container {
    order: 2;
  }
}

@media screen and (max-width: 425px) {
  .contact-container {
    overflow: scroll;
    display: block;
    padding: 1.5rem;
  }

  .contact-container > form > input,
  textarea,
  .contact-container > form > label {
    font-size: 90%;
  }

  .handles {
    margin-top: 2rem;
    margin-left: 0;
    height: 20%;
  }

  .handles > a {
    font-size: medium;
    margin: 0.2rem;
  }
}

@media screen and (max-height: 568px) {
  .contact-container {
    height: 92%;
  }
}

#experiences {
  display: flex;
  flex-direction: column;
}

#experiences > h1 {
  color: #410033;
  font-weight: 800;
}

#experiences > h1 > hr {
  border-top: 2px solid #74025b;
}

.experiences-container {
  padding: 1rem;
  height: 95%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sub-experience {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.sub-experience > .works {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 480px;
  width: 400px;
  background-image: linear-gradient(to bottom, #74025b, #272727 90%);
  border: 2px solid #74025b;
  border-radius: 3px;
  z-index: 0;
  opacity: 0.5;
  box-shadow: 1px 5px 20px 1px #0000009c;
  -webkit-filter: blur(3px) grayscale(0.7);
          filter: blur(3px) grayscale(0.7);
  transition: 0.25s ease-in-out;
}

.sub-experience > .works.active {
  z-index: 5;
  opacity: 1;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-filter: none;
          filter: none;
}

.sub-experience > .works.prev {
  -webkit-transform: translate(-120%, -50%) scale(0.8);
          transform: translate(-120%, -50%) scale(0.8);
  -webkit-mask-image: linear-gradient(to left, #000000 50%, transparent 100%);
          mask-image: linear-gradient(to left, #000000 50%, transparent 100%);
}

.sub-experience > .works.next {
  -webkit-transform: translate(20%, -50%) scale(0.8);
          transform: translate(20%, -50%) scale(0.8);
  -webkit-mask-image: linear-gradient(to right, #000000 50%, transparent 100%);
          mask-image: linear-gradient(to right, #000000 50%, transparent 100%);
}

.sub-experience > .works > h2 {
  background-color: #272727;
  color: #fff;
  font-size: x-large;
  font-weight: 500;
  text-align: center;
  padding: 0.5rem;
  border-radius: 3px 3px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sub-experience > .works > h2 > svg {
  margin-right: 0.5rem;
}

.work-list {
  padding: 0.5rem;
  color: #fff;
}

.work-list > h4 {
  margin: 1.5rem 0 0.2rem 0;
  letter-spacing: 2px;
}

.work-list > p {
  background-color: #ffffff1f;
  padding: 0.5rem;
}

.sub-experience > button {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  background-color: #74025b;
  border: 1px solid #fff;
  color: #fff;
  font-size: larger;
  border-radius: 50%;
  z-index: 8;
  transition: 0.25s ease-in-out;
}

.sub-experience > button:hover {
  background-color: #272727;
}

.sub-experience > .prev-btn {
  left: 35%;
}

.sub-experience > .next-btn {
  right: 35%;
}

@media screen and (max-width: 1024px) {
  .sub-experience > .prev-btn {
    left: 10%;
  }

  .sub-experience > .next-btn {
    right: 10%;
  }
}

@media screen and (max-width: 768px) {
  .experiences-container {
    order: 2;
  }

  .sub-experience > .works {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .work-list > h4 {
    margin: 0.5rem;
  }
}

@media screen and (max-width: 425px) {
  .experiences-container {
    padding: 2px;
    font-size: 70%;
  }

  .sub-experience > .works {
    width: 90%;
  }

  .sub-experience > .works > h2 {
    font-size: medium;
  }

  .sub-experience > button {
    -webkit-transform: translateY(230px);
            transform: translateY(230px);
    border-radius: 5px;
    height: 25px;
    top: auto;
    bottom: 50%;
  }

  .sub-experience > .prev-btn {
    left: 30%;
  }

  .sub-experience > .next-btn {
    right: 30%;
  }
}

@media screen and (max-height: 568px) {
  .experiences-container {
    height: 92%;
  }

  .sub-experience > button {
    -webkit-transform: translateY(215px);
            transform: translateY(215px);
  }
}

#interests {
  display: flex;
  flex-direction: column;
  background-color: #410033;
}

.interests-container {
  padding: 3rem;
  height: 95%;
  overflow: scroll;
}

.interests-container > h4 {
  font-size: medium;
  font-weight: 500;
  color: #fff;
  letter-spacing: 3px;
  padding: 0.5rem;
  border-radius: 2px;
  margin-bottom: 1rem;
  background-color: #ffffff1f;
}

.hobbies {
  height: 200px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.hobbies > .hobby-img {
  height: 80px;
  width: 80px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-bottom: 0.5rem;
  box-shadow: 1px 2px 8px 1px #414141b9;
}

.hobbies > .hobby-img > svg {
  color: #fff;
  font-size: 2.5rem;
  -webkit-animation: skillIn ease-out 1s;
          animation: skillIn ease-out 1s;
}

.hobbies > .hobby-img > img {
  width: 110%;
}

.hobbies > p {
  color: #fff;
  font-size: medium;
  text-align: center;
  letter-spacing: 2px;
}

.grid-container,
.achGrid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(11rem, 1fr));
  grid-gap: 2rem;
}

.achGrid-container a {
  padding: 1rem;
  background-color: #ffffff1f;
  border-radius: 3px;
  color: #fff;
  text-decoration: none;
}

.achGrid-container a:hover {
  background-color: #74025b8a;
}

.achievement-img {
  max-height: 200px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 1rem;
  -webkit-mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
          mask-image: linear-gradient(to bottom, #000000 20%, transparent 100%);
}

.achievement-img > img {
  width: 100%;
}

.achGrid-container a h4 {
  letter-spacing: 2px;
}

.achGrid-container a p {
  font-size: small;
}

.interests-container::-webkit-scrollbar {
  width: 0;
}

@media screen and (max-width: 1024px) {
  .hobbies > .hobby-img {
    height: 60px;
    width: 60px;
  }

  .hobbies > .hobby-img > svg {
    font-size: 150%;
  }
}

@media screen and (max-width: 425px) {
  .interests-container {
    padding: 2rem;
  }

  .grid-container {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .hobbies > p {
    font-size: 80%;
  }

  .achGrid-container a h4 {
    font-size: 70%;
    font-weight: 600;
  }

  .achGrid-container a p {
    font-size: 70%;
  }
}

@media screen and (max-width: 768px) {
  .interests-container {
    order: 2;
    height: 80vh;
  }
}

@media screen and (max-height: 568px) {
  .interests-container {
    height: 92%;
  }
}

#skills {
  display: flex;
  flex-direction: column;
}

#skills > h1 {
  color: #410033;
  font-weight: 800;
}

#skills > h1 > hr {
  border-top: 2px solid #74025b;
}

.skills-container {
  padding: 3rem;
  height: 95%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 2rem;
  z-index: 1;
}

.programs {
  height: 200px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: #272727;
  border: 2px solid #74025b;
  box-shadow: 1px 2px 8px 1px #414141b9;
}

.programs > .logo-img {
  height: 80px;
  width: 80px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-bottom: 0.5rem;
  box-shadow: 1px 2px 8px 1px #414141b9;
}

.programs > .logo-img > svg {
  color: #74025b;
  font-size: 2.5rem;
  -webkit-animation: skillIn ease-out 1s;
          animation: skillIn ease-out 1s;
}

@-webkit-keyframes skillIn {
  0% {
    -webkit-transform: rotate(-35deg);
            transform: rotate(-35deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes skillIn {
  0% {
    -webkit-transform: rotate(-35deg);
            transform: rotate(-35deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.programs > .logo-img > img {
  width: 110%;
}

.programs > p {
  color: #fff;
  font-size: medium;
  text-align: center;
  letter-spacing: 2px;
}

@media screen and (max-width: 1024px) {
  .skills-container {
    padding: 1rem;
    grid-gap: 1rem;
    overflow: scroll;
  }

  .programs > .logo-img {
    height: 60px;
    width: 60px;
  }

  .programs > .logo-img > svg {
    font-size: 150%;
  }
}

@media screen and (max-width: 768px) {
  .skills-container {
    order: 2;
    height: 80vh;
  }
}

@media screen and (max-width: 425px) {
  .skills-container {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .programs {
    height: 150px;
  }

  .programs > p {
    font-size: 70%;
  }
}

.menu {
  display: none;
}

.navbar > ul {
  height: 5vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  list-style: none;
  padding: 10px;
  position: fixed;
  z-index: 2;
  background-color: #272727;
  -webkit-animation: fadeIn ease 2s;
          animation: fadeIn ease 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    left: 150rem;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    left: 150rem;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

.navbar > ul > li {
  padding: 0.5rem 0.4rem;
  transition: 0.5s ease;
}

.navbar > ul > li:hover {
  background-color: #74025b;
  border-radius: 2px;
}

.navbar > ul > li > a {
  font-size: small;
  text-decoration: none;
  color: #fff;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
}

.navbar > ul > li > a > svg {
  margin-right: 0.5rem;
}

.resume {
  border: 2px solid #74025b;
  border-radius: 2px;
}

.navbar > ul > .resume > a {
  color: #a38605;
  font-weight: bold;
  letter-spacing: 3px;
}

@media screen and (max-width: 768px) {
  .menu {
    display: block;
    position: absolute;
    right: 1rem;
    top: 1rem;
    color: #74025b;
    font-size: xx-large;
    cursor: pointer;
    z-index: 99;
    background-color: #ffffffef;
    border: 2px solid #74025b;
    border-radius: 3px;
    height: 40px;
    width: 40px;
    box-shadow: 1px 1px 5px 0px #00000079;
    -webkit-animation: menuFade ease 0.5s;
            animation: menuFade ease 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  @-webkit-keyframes menuFade {
    0% {
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

  @keyframes menuFade {
    0% {
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

  .navbar > ul {
    position: absolute;
    z-index: 10;
    grid-gap: 15px;
    gap: 15px;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    -webkit-animation: none;
            animation: none;
  }

  .navbar > ul > li {
    padding: 1rem 0.5rem;
    width: 100%;
    background-color: #ffffff17;
    border-radius: 3px;
  }

  .navbar > ul > li:hover {
    color: #2c394b;
  }

  .open {
    opacity: 1;
    left: 0;
    transition: 0.4s ease-in-out;
  }

  .close {
    opacity: 0;
    left: -100rem;
    transition: 0.5s ease-in-out;
  }
}

#projects {
  display: flex;
  flex-direction: column;
  background-color: #410033;
}

.projects-container {
  padding: 3rem;
  height: 95%;
  display: flex;
  direction: rtl;
  overflow-y: scroll;
}

.projects-container::-webkit-scrollbar {
  width: 4px;
}

.projects-container::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey;
  border-radius: 10px;
}

